<template>
    <div id="navbarr">
        <v-toolbar color="red darken-1" dark>
            <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
            <v-toolbar-title class="white--text" v-if="">{{navbarText}}</v-toolbar-title>
            <!-- <v-spacer></v-spacer> -->
            <v-spacer></v-spacer>
                <v-btn icon>
                <v-icon>search</v-icon>
                </v-btn>
                <v-btn icon>
                <v-icon>more_vert</v-icon>
                </v-btn>
        </v-toolbar>
        <v-navigation-drawer
      v-model="drawer"
      absolute
      temporary
    >
      <v-list class="pa-1">
        <v-list-tile avatar>
          <v-list-tile-avatar>
            <img src="https://randomuser.me/api/portraits/men/80.jpg">
          </v-list-tile-avatar>

          <v-list-tile-content>
            <v-list-tile-title>Adeniran Opeyemi</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>

      <v-list class="pt-0" dense>
        <v-divider></v-divider>

        <v-list-tile
          v-for="item in items"
          :key="item.title"
          @click=""
        >
          <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>

          <v-list-tile-content>
            <v-list-tile-title>{{ item.title }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    </div>
    
</template>
<script>
import bottomnav from '../appShell/bottomnav';
export default {
	components: {
		bottomnav: bottomnav,
	},
	name: 'navbar',
	data() {
		return {
			// navitem: bottomNav,
			navbarText: bottomNav,
			drawer: null,
			items: [{ title: 'Home', icon: 'dashboard' }, { title: 'About', icon: 'question_answer' }],
		};
	},
};
</script>
<style scoped>
</style>




