<template>
    <div class="py-3">
        <v-card>
            <v-card-title>

                <!-- Loader Icon -->
               <!--  <div v-if="loading === true">
                    <h1>Loading</h1>
                </div> -->
                <div v-if="loading === true" class="page">
                    <skeleton-loading>
                        <row v-for="i in 4"
                            :key="i"
                            :gutter="{top: '10px', bottom: '10px'}">
                            <column :span="4">
                                <circle-skeleton></circle-skeleton>
                            </column>
                            <column :span="20" :gutter="20">
                                <square-skeleton 
                                    :boxProperties="{
                                        top: '10px',
                                        width: '70px',
                                        height: '15px'
                                    }"
                                >
                                </square-skeleton>
                                <square-skeleton 
                                    :boxProperties="{
                                        width: '250px',
                                        height: '15px',
                                        top: '10px'
                                    }"
                                >
                                </square-skeleton>
                            </column>
                        </row>
                    </skeleton-loading>
                </div>

                <div class="wrapper">
                    <div class="row">
                        <h1>{{cryptoOutputs}}</h1>
                    </div>
                </div>
                <!-- Loading -->
                <v-data-table class="table-fw"
                    :items="crytoElement"
                    hide-actions
                    hide-headers
                >
                    <template slot="items" slot-scope="props">
                        <tr class="tr-border">
                            <td class="pl-0 pr-1">
                            <img class="crypto-icon" v-bind:src="props.item.imgUrl"/>
                            </td>
                            <td class="text-xs-right px-1">{{ props.item.name }}</td>
                            <td class="text-xs-right px-1">
                                <trend
                                    :data="[0, 2, 5, 9, 5, 10, 3, 5, 0, 0, 1, 8, 2, 9, 0]"
                                    :gradient="['#000000', '#000000', '#000000']"
                                    auto-draw
                                    :height="60"
                                    smooth>
                                </trend>
                            </td>
                            <td class="text-xs-right px-1 pric-w">{{ props.item.price }}</td>
                            <td class="text-xs-right crypto-volume">{{ props.item.volume }}</td>
                        </tr>
                        <tr class="tr-border">
                            <td class="pl-0 pr-1">
                            <img class="crypto-icon" v-bind:src="props.item.imgUrl"/>
                            </td>
                            <td class="text-xs-right px-1">{{ props.item.name }}</td>
                            <td class="text-xs-right px-1">
                                <trend
                                    :data="[0, 2, 5, 9, 5, 10, 3, 5, 0, 0, 1, 8, 2, 9, 0]"
                                    :gradient="['#000000', '#000000', '#000000']"
                                    auto-draw
                                    :height="60"
                                    smooth>
                                </trend>
                            </td>
                            <td class="text-xs-right px-1 pric-w">{{ props.item.price }}</td>
                            <td class="text-xs-right crypto-volume">{{ props.item.volume }}</td>
                        </tr>
                    </template>
                </v-data-table>
            </v-card-title>
        </v-card>
    </div>
</template>

<script>
import axios from 'axios';
export default {
	name: 'marketdata',
	data() {
		return {
			cryptoOutputs: [],
			loading: true,
			crytoElement: [
				{
					value: false,
					imgUrl:
						'https://cdn-images.threadless.com/threadless-media/artist_shops/shops/ethereum/profile/logo-1466683734-2ed9d9dc9e3f3846050cd518b2516b8e.png?v=3&d=eyJvbmx5X21ldGEiOiBmYWxzZSwgImZvcmNlIjogZmFsc2UsICJvcHMiOiBbWyJyZXNpemUiLCBbNDUwXSwge31dXX0=',
					name: 'ETH',
					price: '$ 1500',
					chart: 'Chart View',
					volume: 60 + '%',
				},
			],
		};
	},
	mounted: function() {
		axios.get('https://min-api.cryptocompare.com/data/price?fsym=BTC&tsyms=USD,JPY,EUR').then(
			response => {
				this.loading = true;
				this.cryptoOutputs = response.data;
                if (this.cryptoOutputs == response.data) {
                    // alert("Equal Data");
                    this.loading = false;
                }
				// alert('Hello World');
			},
			error => {
                // alert("Error Loading");
				this.loading = true;
			}
		);
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.crypto-icon {
	width: 30px;
}
.crypto-volume {
	color: red;
}
.table-fw {
	width: 94vw;
}
.tr-border {
	border-bottom: 1px solid gainsboro;
	height: 60px;
}
.pric-w {
	width: 17%;
}
</style>



