<template>
    <div>
        <v-card>
        <v-container
          fluid
          grid-list-md
        >
          <v-layout row wrap>
            <v-flex
              v-for="card in cards"
              v-bind="{ [`xs${card.flex}`]: true }"
              :key="card.title"
            >
              <v-card>
                <v-card-media
                  :src="card.src"
                  height="200px"
                >
                  <v-container
                    fill-height
                    fluid
                    pa-2
                  >
                    <v-layout fill-height>
                      <v-flex xs12 align-end flexbox>
                      	<span class="categ" v-text="card.category"></span>
                        <span class="headline white--text" v-text="card.title"></span>
                      </v-flex>
                    </v-layout>
                  </v-container>
                </v-card-media>
              </v-card>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card>
    </div>
</template>

<script>
export default {
	name: 'news',
	data() {
		return {
			cards: [
		        { title: 'Favorite road trips', src: 'https://cdn.vuetifyjs.com/images/cards/road.jpg', flex: 6, category: 'BITCOIN'},
		        { title: 'Best airlines', src: 'https://cdn.vuetifyjs.com/images/cards/plane.jpg', flex: 6, category: 'ETHERUEM'},
		        { title: 'Best airlines', src: 'https://cdn.vuetifyjs.com/images/cards/plane.jpg', flex: 6, category: 'ETHERUEM'},
		        { title: 'Favorite road trips', src: 'https://cdn.vuetifyjs.com/images/cards/road.jpg', flex: 6, category: 'BITCOIN'}
		      ]
		};
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style>
.headline {
	font-weight: bold!important;
    font-size: 18px!important;
    line-height: 22px!important;
}
.categ{
	text-transform: uppercase;
	display: block;
    color: white;
    font-size: 12px;
    letter-spacing: 3px;
    font-weight: bold;
    margin-bottom: 8px;
}
</style>



