<template>
    <div id="">
      <!-- Navbar -->
          <div id="navbarr">
        <v-toolbar color="red darken-1" dark>
            <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
            <v-toolbar-title class="white--text" v-if="">{{navbarText}}</v-toolbar-title>
            <!-- <v-spacer></v-spacer> -->
            <v-spacer></v-spacer>
                <v-btn icon>
                <v-icon>search</v-icon>
                </v-btn>
                <v-btn icon>
                <v-icon>more_vert</v-icon>
                </v-btn>
        </v-toolbar>
        <v-navigation-drawer
      v-model="drawer"
      absolute
      temporary
    >
      <v-list class="pa-1">
        <v-list-tile avatar>
          <v-list-tile-avatar>
            <img src="https://randomuser.me/api/portraits/men/80.jpg">
          </v-list-tile-avatar>

          <v-list-tile-content>
            <v-list-tile-title>Adeniran Opeyemi</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>

      <v-list class="pt-0" dense>
        <v-divider></v-divider>

        <v-list-tile
          v-for="item in items"
          :key="item.title"
          @click=""
        >
          <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>

          <v-list-tile-content>
            <v-list-tile-title>{{ item.title }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    </div>

      <!-- Navbar -->
      <v-tabs 
    centered
    color="white"
    dark
    icons-and-text
  >
  
    <v-tabs-slider color="red darken-1"></v-tabs-slider>

    <v-tab  class="navi-tab" href="#market" v-on:click="navbarText='Market Data'">
      Market
      <v-icon color="grey lighten-1">line_style</v-icon>
    </v-tab>

    <v-tab  class="navi-tab" href="#news" v-on:click="navbarText='News'">
      News
      <v-icon color="grey lighten-1">chat</v-icon>
    </v-tab>

    <v-tab  class="navi-tab" href="#chart" v-on:click="navbarText='Chart'">
      Chart
      <v-icon color="grey lighten-1">bar_chart</v-icon>
    </v-tab>

    <v-tab  class="navi-tab" href="#pricing" v-on:click="navbarText='Pricing'">
      Pricing
      <v-icon color="grey lighten-1">credit_card</v-icon>
    </v-tab>



    <!-- Market Tab Item -->
    <v-tab-item :id="'market'">
      <v-card flat>
        <market />
      </v-card>
    </v-tab-item>

    <!-- News Tab Item -->
    <v-tab-item :id="'news'">
      <v-card flat>
        <news />
      </v-card>
    </v-tab-item>

    <!-- News Tab Item -->
    <v-tab-item :id="'chart'">
      <v-card flat>
        <chart />
      </v-card>
    </v-tab-item>

    <!-- Chart Tab Item -->
    <v-tab-item :id="'pricing'">
      <v-card flat>
        <pricing />
      </v-card>
    </v-tab-item>


    <!-- <v-tab-item v-for="navtab in navtabs" :id="navtab">
      <v-card flat>
        <div v-html="navtab"></div>
      </v-card>
    </v-tab-item> -->


    <v-tab-item
      v-for="i in 4"
      :id="'tab-' + i"
      :key="i"
    >
      <v-card flat>
        <v-card-text>{{ text }}</v-card-text>
      </v-card>
    </v-tab-item>
  </v-tabs>
      
    </div>
</template>
<script>
import marketdata from '../tabComponent/marketdata';
import chart from '../tabComponent/chart';
import news from '../tabComponent/news';
import pricing from '../tabComponent/pricing';

export default {
	components: {
		market: marketdata,
		chart: chart,
		news: news,
		pricing: pricing,
	},
	name: 'bottomnav',
	data() {
		return {
			navbarText: 'Market Data',
			drawer: null,
			navtabs: ['market', 'news', 'chart', 'pricing'],
			text:
				'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.',
			items: [{ title: 'Home', icon: 'dashboard' }, { title: 'About', icon: 'question_answer' }],
		};
	},
};
</script>
<style scoped>
.navi-tab {
	text-transform: capitalize !important;
}
.tab-position {
	position: fixed;
	width: 100%;
	z-index: 20000;
	bottom: 0;
}
</style>




